module.exports = function wfetch(url, options, responseCallback, errorCallback){
    let response = fetch(url, options);
    response.then(response => response.json()).then(response => {
        if(response.error === "NOT AUTHORIZED"){
            window.location.href = "/login";
            return;
        }
        responseCallback(response);
    }).catch(error => {
        console.log(error);
        errorCallback(error);
    });
    return response;
}