import { useEffect } from 'react';
import { useForm } from 'react-hook-form';

export default function CustomerEdit(props) {

    const { register, handleSubmit, errors, reset } = useForm();
    
    useEffect(() => {
        console.log(props.details);
        reset(props.details);
    }, [props.details, reset]);

    return (
        <div className="col p-0 card my-2">
            <div className="card-header text-dark">
                <h5>Customer Information</h5>
            </div>
            <div className="card-body p-2">
                {getForm(props, register, handleSubmit, errors)}
            </div>
        </div>
    );
}

 function onSubmit(props, data){
    let dateOfBirth = data.dateOfBirth.split('-');
    let dob = {
        year: parseInt(dateOfBirth[0]),
        month: parseInt(dateOfBirth[1]),
        day: parseInt(dateOfBirth[2])   
    }
    dateOfBirth = {
        string: data.dateOfBirth,
        ...dob
    }
    data.dateOfBirth = dateOfBirth;

    data.validated = true;
    props.update(data);
} 

function getForm(props, register, handleSubmit, errors){
    if(props.details == null){
        return (<div className="row p-5">
            <h3 className="text-light">No details found</h3>
        </div>);
    }
    return (
        <form onSubmit={handleSubmit(onSubmit.bind(null, props))}>
            <div className="form-group col">
                <div className="row py-1">
                    <p className="text-dark p-1">Personal Details</p>
                </div>
                <div className="row py-1">
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        <input type="text" className="form-control" id="firstName" 
                                name="firstName" placeholder="First" ref={register({required: true})}/>
                    </div>
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        <input type="text" className="form-control" id="lastName" 
                                name="lastName" placeholder="Last" ref={register({required: true})}/>
                    </div>
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        {errors.firstName && <small className="text-danger">*First Name Required</small>}
                        {errors.lastName && <small className="text-danger">*Last Name Required</small>}
                    </div>
                </div>

                <div className="row py-1">
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        <input type="email" className="form-control" id="email" 
                                name="email" placeholder="Email" ref={register({required: true})}/>
                    </div>
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        <input type="tel" className="form-control" id="phone" 
                                name="phone" placeholder="Mobile" ref={register}/>
                    </div>
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        {errors.email && <small className="text-danger">*Email id is required</small>}
                    </div>
                </div>
                <div className="row py-1">
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        <input type="date" className="form-control" id="dateOfBirth" 
                                name="dateOfBirth" max={new Date().toLocaleDateString()} 
                                placeholder="Date of Birth" ref={register({required: true})}/>
                        <small className="text-dark">Date of Birth</small>
                    </div>
                    <div className="col-sm-0 col-lg-4 col-md-0 py-1"></div>
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        {errors.dateOfBirth && <small className="text-danger">Date of Birth required</small>}
                    </div>
                </div>
                <div className="row py-1 mt-2">
                    <p className="text-dark p-1">Address</p>
                </div>
                <div className="row py-1">
                    <div className="col-sm-12 col-lg-8 col-md-12 py-1">
                        <input type="text" className="form-control" id="street" 
                                name="street" placeholder="Address Line 1" ref={register({required: true})}/>
                    </div>
                    <div className="col-sm-12 col-lg-8 col-md-12 py-1">
                        <input type="text" className="form-control" id="street2" 
                                name="street2" placeholder="Address Line 2" ref={register}/>
                    </div>
                    <div className="col-sm-12 col-lg-4 col-md-12 py-1">
                        {errors.street && <small className="text-danger">Address required, atleast line 1</small>}
                    </div>
                </div>
                <div className="row py-1">
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        <input type="text" className="form-control" id="city" 
                                name="city" placeholder="City" ref={register({required: true})}/>
                    </div>
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        <input type="text" className="form-control" id="zip" 
                                name="zip" placeholder="Zip" ref={register({required: true})}/>
                    </div>
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        {
                            (() => {
                                let {city, zip, state, country} = errors;
                                let requirements = [];
                                city && requirements.push("City");
                                zip && requirements.push("Zip");
                                state && requirements.push("State");
                                country && requirements.push("Country");
                                if(requirements.length > 0){
                                    return (<small className="text-danger">{requirements.join(', ') + " required"}</small>);
                                }
                            })()
                        }
                    </div>
                </div>
                <div className="row py-1">
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        <input type="text" className="form-control" id="state" 
                                name="state" placeholder="State" ref={register({required: true})}/>
                    </div>
                    <div className="col-sm-12 col-lg-4 col-md-6 py-1">
                        <input type="text" className="form-control" id="country" 
                                name="country" placeholder="Country" ref={register({required: true})}/>
                    </div>
                    <div className="col-sm-12 col-lg-3 col-md-12 py-1">
                        <button type="sumbit" className="form-control btn btn-primary">Update Info</button>
                    </div>
                </div>
            </div>
        </form>
    );
}