export default function customerDetails(props) {
    let edit = function (){
        props.edit(props.details);
    }

    return (
        <div className='card'>
            <div className='card-header'>
                Customer information
                <button className='float-right btn btn-link btn-sm' onClick={edit}>Edit</button>
            </div>
            <div className='card-body'>
                <div className="row">
                    <div className="col">
                        <p>
                            <b>{props.details.firstName + " " + props.details.lastName}</b>
                        </p>
                        <p>{props.details.email}{(props.details.phone)? ", " + props.details.phone: ""}</p>
                        <p>{props.details.dateOfBirth.string}</p>
                        <p className="mb-0">
                            <small>{props.details.street}</small>
                        </p>
                            {props.details.street2? (<p className="mt-0"><small>{props.details.street2}</small></p>): ""}
                    </div>
                    <div className="col">
                        <p>{props.details.city}</p>
                        <p>{props.details.state + ", " + props.details.country}</p>
                        <p>{props.details.zip}</p>
                    </div>
                </div>
            </div>
        </div>
    );
}