import { useGoogleLogout } from 'react-google-login';
import {useHistory} from 'react-router-dom';
const fetch = require('../scripts/fetchSessionChecker');

export default function(){
    let {signOut, loaded} = useGoogleLogout({
        clientId: "584685314295-m141d4bsqmmc15qq9mc7lfbfmrosng8i.apps.googleusercontent.com",
        onLogoutSuccess: function(){
            fetch("/api/sessions", {
            method: "DELETE",
            headers: {
                'Content-Type': 'application/json'
            }
            }, json => {
                let history = useHistory();
                history.push("/login");
            }, error => {
                console.log(error);
            });
        }
    });
    if(loaded){
        signOut();
    }
    return (<div></div>);
}