import React from 'react';
import { withRouter } from 'react-router-dom';
import PropTypes from 'prop-types';
import checkout from '../scripts/paysafecheckout';
import CustomerDetails from '../components/CustomerDetails';
import CustomerEdit from '../components/CustomerEdit';

const fetch = require('../scripts/fetchSessionChecker');

class Cart extends React.Component {
    static propTypes = {
        match: PropTypes.object.isRequired,
        location: PropTypes.object.isRequired,
        history: PropTypes.object.isRequired
    }

    constructor(props){
        super(props);
        let cart = [];
        if(props.cart != null && Array.isArray(props.cart)){
            cart = props.cart;
        }
        this.state = {
            cart,
            customer: null
            // customer: JSON.parse('{"firstName":"Infant","lastName":"Antony Shelton","email":"sheltonanton@gmail.com","phone":"","dateOfBirth":{"string": "1997-01-12", "year":1997,"month":1,"day":12},"street":"F3 Royal Paradise Gandhi Nagar","street2":"IAF Road Selaiyur","city":"San Fransisco","zip":"58769","state":"California","country":"United States","validated":true}')
        }
    }

    componentDidMount = () => {
        const {location} = this.props;
        let item = location.item;
        let cart = this.state.cart;
        if(item != null){
            cart.push(item);
        }
        fetch('/api/customers', {
            method: "GET",
            headers: {
                'Content-Type': 'application/json'
            }
        }, json => {
            this.setState({cart: [...cart], customer: json});
        }, error => {
            console.log(error);
        });
    }

    removeItem = (item) => {
        let cart = this.state.cart.filter(i => i.id != item.id);
        this.setState({cart, customer: this.state.customer});
    }

    filterCustomerInfo = (detail, id) => {
        let {
            merchantCustomerId,
            firstName,
            lastName,
            dateOfBirth: {
                year,
                month,
                day
            },
            email
        } = detail;
        let result = {
            merchantCustomerId,
            firstName,
            lastName,
            dateOfBirth: {
                year,
                month,
                day
            },
            email
        }
        if(id){
            result.id = id;
        }
        return JSON.stringify(result);
    }

    filterAddressInfo = (detail, address_id) => {
        let {id, street, street2, city, state, country, zip} = detail;
        let result = {customer_id: id, street, street2, city, state, country, zip};
        if(address_id){
            result.address_id = address_id;
        }
        return JSON.stringify(result);
    }

    updateAddressDetail = (detail) => {
        fetch(`/api/customers/${detail.customer_id}/addresses${detail.address_id? "/"+detail.address_id: ""}`, {
            method: (detail.address_id)? "PUT": "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: this.filterAddressInfo(detail, detail.address_id)
        }, (json) => {
            detail.address_id = json.id;
        }, (error) => {
            console.log(error);
        });
    }

    updateCustomerDetail = (detail) => {
        detail = Object.assign(this.state.customer || {}, detail);
        this.setState({cart: this.state.cart, customer: detail});

        fetch(`/api/customers/${detail.customer_id? detail.customer_id: ""}`, {
            method: (detail.customer_id)? "PUT": "POST",
            headers: {
                'Content-Type': 'application/json'
            },
            body: this.filterCustomerInfo(detail, detail.customer_id)
        }, (json) => {
            detail.customer_id = json.id;
            this.updateAddressDetail(detail);
        }, error => console.log(error));
    }

    editCustomerDetail = (detail) => {
        detail = Object.assign(this.state.customer || {}, detail);
        let customer = Object.assign({}, detail, {validated: false});
        this.setState({cart: this.state.cart, customer});
    }

    placeOrder = (total) => {
        checkout(total, new Date().getTime(), this.state.customer).then(result => {
            fetch('/api/payments/', {
                method: "POST",
                headers: {
                    'Content-Type': 'application/json'
                },
                body: JSON.stringify(result)
            }, json => {
                console.log(json);
            }, error => {
                alert("Error while processing payment");
            });
            return true;
        }, error => {
            console.log(error);
            alert("PaySafe checkout failed");
            return false;
        });
    }

    render = () => {
        let cartDetails = this.getCartDetails();
        let orderSummary = this.getOrderDetails();
        let customerDetails = this.getCustomerDetails();
        return (
            <section>
            <div className="row">
                <div className="col-lg-9 col-md-9 col-sm-12">
                    <div className="row">
                        <div className="col-lg-12 col-md-12 col-sm-12">
                            {cartDetails}
                        </div>
                        <div className="col-12"><hr></hr></div>
                        <div className="col-lg-12 col-md-12 col-sm-12">
                            {customerDetails}
                        </div>
                    </div>
                </div>
                <div className="col-lg-3 col-md-3 col-sm-12">
                    {orderSummary}
                </div>
            </div>
            </section>
        );
    }

    getCartDetails = () => {
        let cart = this.state.cart;
        let cartDetails = cart.map(item => {
            return (
                <div key={item.id} className="card my-2">
                    <div className="row">
                        <div className="col-4">
                            <img src={item.images.background} alt={item.name} className="img-fluid" style={{width: "200px"}}/>
                        </div>
                        <div className="col-5">
                            <div className="card-body" style={{height: "85%"}}>
                                <h5 className="card-title">{item.name}</h5>
                                <section className="card-text">
                                    <p>{item.description}</p>
                                    <p><b>{item.type} (type)</b></p>
                                </section>
                            </div>
                        </div>
                        <div className="col-3 text-right">
                            <p className="card-text p-4 align-bottom">
                                <big className="text-muted">
                                    {"$" + item.price}
                                </big>
                            </p>
                            <button className="btn btn-danger btn-sm m-4" onClick={this.removeItem.bind(this, item)}>
                                &#10007;
                            </button>
                        </div>
                    </div>
                </div>
            );
        });
        if(cart.length == 0){
            cartDetails = (
                <div className="text-center p-4">
                <h2 className="text-muted">Feeling empty!</h2>
                <img src={'/feeling_empty.png'} style={{width: 80, height: 80, opacity: 0.2}} alt="Feeling Empty"/>
                </div>
            );
        }
        return cartDetails;
    }

    getOrderDetails = () => {
        let total = 0;
        let orderSummary = (
            <div>
                <table className="table my-2" style={{height: "70%"}}>
                    <thead>
                        <tr>
                            <th>Item</th>
                            <th className="text-right">Cost</th>
                        </tr>
                    </thead>
                    <tbody>
                    {
                        this.state.cart.map(item => {
                            total += item.price;
                            return (<tr key={item.id}>
                                <td>{item.name}</td>
                                <td className="text-right">{"$" + item.price}</td>
                            </tr>);
                        })
                    }
                    </tbody>
                    <tfoot>
                        <tr>
                            <td><i>Total</i></td>
                            <td className="text-right">{"$" + total}</td>
                        </tr>
                    </tfoot>
                </table>
                <div className="text-right">
                    <button 
                        className={
                            "btn btn-sm btn-outline-success m-1" + (
                                (
                                    this.state.cart.length == 0 ||
                                    this.state.customer == null || 
                                    (!this.state.customer.validated)
                                )? " disabled": "")
                            } 
                        onClick={
                            (
                                this.state.cart.length == 0 ||
                                this.state.customer == null || 
                                (!this.state.customer.validated)
                            )? null: this.placeOrder.bind(this, total)
                        }

                        title={
                            (
                                this.state.cart.length == 0 ||
                                this.state.customer == null || 
                                (!this.state.customer.validated)
                            )? "Provide proper customer information": null
                        }
                    >
                            Place Order
                    </button>
                </div>
            </div>
        );
        return orderSummary;
    }

    serializeCustomerInfo = (details) => {
        details = details || {};
        if(details.firstName == null && details.lastName == null && details.name != null && typeof details.name === 'string'){
            let names = details.name.split(' ');
            let firstName = names[0];
            let lastName = (names.length > 1)? names.slice(1).join(' '): null;
            details.firstName = firstName;
            details.lastName = lastName;
        }
        if(typeof details.dateOfBirth == "object" && details.dateOfBirth != null){
            details.dateOfBirth = details.dateOfBirth.string || (()=>{
                let d = details.dateOfBirth;
                d.month = String(d.month);
                d.year = String(d.year);
                d.month = (d.month.length == 1)? "0"+d.month: d.month;
                d.day = (d.day.length == 1)? "0"+d.day: d.day;
                return d.year + "-" + d.month + "-" + d.day;
            })();
        }

        return details;
    }

    getCustomerDetails = () => {
        let warning = "";
        let notvalid = this.state.customer == null || !(this.state.customer.validated);
        if(notvalid){
            warning = <small className="text-danger p-1">
                <b>&#x26A0; Specify your right information to enable 'Place Order'</b>
                </small>
        }
        return (
            <section>
                {warning}
                {(notvalid)? (<CustomerEdit details={this.serializeCustomerInfo(this.state.customer)} update={this.updateCustomerDetail}/>):(<CustomerDetails details={this.state.customer} edit={this.editCustomerDetail}/>)}
            </section>
        );
    }
}

export default withRouter(Cart);