import btoa from 'btoa';

const API_KEY = btoa("public-7751:B-qa2-0-5f031cbe-0-302d021500890ef262296563accd1cb4aab790323d2fd570d30214510bcdacdaa4f03f59477eef13f2af5ad13e3044");

export default function checkout(amount, hash, extra){
    return new Promise((resolve, reject) => {
        function resultCallback(instance, error, result){
            if(error){
                reject(error);
                instance.showFailureScreen("Error in purchase");
                alert("Error in payment");
            }else{
                result.merchantRefNum = options.merchantRefNum;
                result.amount = options.amount;
                result.currencyCode = options.currency;
                instance.showSuccessScreen("Purchased successfully");
            }
            if(instance.isOpen()){
                setTimeout(() => instance.close(), 3000);
                alert("Payment successfully completed");
            }
            resolve(result);
        }
        function closeCallback(stage, expired){
            if(expired) reject(new Error("Checkout expired"));
            return false;
        }

        var options = {
            currency: "USD",
            merchantRefNum: String(hash),
            amount: amount * 100,
            billingAddress: { //remove this
                nickName: extra.firstName + " " + extra.lastName,
                street: extra.street,
                street2: extra.street2,
                city: extra.city,
                zip: extra.zip,
                country: extra.country,
                state: extra.state
            },
            environment: "TEST",
            customer: {
                firstName: extra.firstName,
                lastName: extra.lastName,
                email: extra.email,
                phone: extra.phone,
                dateOfBirth: {
                    day: extra.dateOfBirth.day,
                    month: extra.dateOfBirth.month,
                    year: extra.dateOfBirth.year
                }
            },
            displayPaymentMethods: ['card'],
            paymentMethodDetails: {
                paysafecard: {
                    consumerId: extra.email
                }
            }
        }

        global.paysafe.checkout.setup(API_KEY, options, resultCallback, closeCallback);
    });
}